






























export default {
	data() {
		return {
			list: [],
			index: 0,
			info: {},
			amount: 0,
			time: ''
		};
	},
	onShow() {
		this.info = this.$store.state.globalData.userInfo.member
		this.getList()
	},
	onLoad(options){
		this.time = options.time
	},
	methods: {
		getList() {
			var that = this
			this.$api.get('/api/bankcard/getList', {}, function(res) {
				console.log(res)
				if (res.data.code == 200) {
					var list = res.data.data
					if (list.length > 0) {
						for (var i in list) {
							list[i].card_no = list[i].bank + ' ' + list[i].card_no.substr(0, 4) + ' **** ' + list[i].card_no.substr(
								list[i].card_no.length -
								4, list[i].card_no.length);
						}
						that.list = list
						console.log(that.list)
					}

				} else {
					uni.showToast({
						title: res.data.msg,
						icon: "none"
					})
				}
			})
		},
		bindPickerChange: function(e) {
			this.index = e.target.value
		},

		getAmount(e) {
			var val = e.detail.value
			var canUse = this.info.user_coin_profit * 100 / 100
			var idx = String(val).indexOf(".") + 1; //获取小数点的位置
			var count = String(val).length - idx; //获取小数点后的个数
			if (idx > 0 && count > 2) {
				this.amount = 0
				uni.showToast({
					title: "最多输入两位小数，请重新输入",
					icon: "none"
				})
			} else if (val > canUse) {
				this.amount = 0
				uni.showToast({
					title: "已超出最大可提现数量，请重新输入",
					icon: "none"
				})
			} else {
				this.amount = e.detail.value
			}
		},
		applyWithdraw() {
			var that = this
			if (this.amount == 0) {
				return uni.showToast({
					title: "请重新输入提现金额",
					icon: "none"
				})
			}
			uni.showModal({
				title: '确认提现￥' + this.amount + '吗？',
				content: '',
				confirmColor: '#e9101f',
				success: function(res) {
					if (res.confirm) {
						that.$api.post('/api/finance/applyCoinWithdraw', {
							amount: that.amount,
							bank_card_id: that.list[that.index].id
						}, function(res) {
							console.log(res)
							if (res.data.code == 200) {
								uni.showToast({
									title: '提现申请成功！',
								})
								setTimeout(function() {
									uni.navigateBack()
								}, 800)
							} else {
								uni.showToast({
									title: res.data.msg,
									icon: "none"
								})
							}
						})
					} else if (res.cancel) {
						console.log('用户点击取消');
					}
				}
			})
		}
	}
}
